@media (max-width: 800px) {
  .parts__parts-header {
    overflow-x: scroll;
  }

  .scroll_inner {
    min-width: 400px;
  }

  .parts__parts-header h1 {
    font-size: 12px;
    line-height: 1.5em;
  }

  .parts__parts-header h1:nth-of-type(2) {
    border-left: 2px solid var(--text-primary);
    border-right: 2px solid var(--text-primary);
  }
}

.parts {
  width: 100%;
}

.parts__parts-header {
  box-sizing: border-box;
  position: sticky;
  top: 0;
  width: 100%;
  background-color: var(--background-primary);
  border-bottom: 1px solid var(--text-primary);
  padding: 0 2em;
}

.scroll_inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  align-items: center;
  color: var(--text-primary);
  height: 50px;
}
