@import url(./reset.css);
@import url(./header.css);
@import url(./main_page.css);
@import url(./parts.css);
@import url(./course_info.css);
@import url(./lesson_page.css);

* {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

html[data-theme="light"] {
  --text-primary: #272e38;
  --background-primary: #fff;
  --text-secondary: #9b9b9b;
  --background-secondary: #272e38;
  --text-highlight: #cb4757;
  --background-highlight: #fdca8b;
  --code-text: #7a7a7a;
  --code-background: #e9e9e9;
}

html[data-theme="dark"] {
  --text-primary: #fff;
  --background-primary: #272e38;
  --text-secondary: #9b9b9b;
  --background-secondary: #272e38;
  --text-highlight: #cb4757;
  --background-highlight: #fdca8b;
  --code-text: #9f9f9f;
  --code-background: #4d4d4d;
}

.App {
  background-color: var(--background-primary);
}

.lesson {
  line-height: 2.5;
}
.lesson_anchor {
  border: 1px solid black;
  background-color: var(--primary);
  cursor: pointer;
}
