@media (max-width: 1220px) {
  .main {
    line-height: 20px;
  }

  .width_limiter {
    padding: 0 2em;
  }
}

.main {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
}

.main__navigation {
  width: 100%;
  padding: 5em 0 3em;
  background-color: var(--background-secondary);
}

.main__navigation_title {
  font-size: 2em;
  font-weight: bold;
  color: var(--background-highlight);
  margin: 0 0 2em;
  line-height: 1em;
}

.main__navigation_subtitle {
  font-size: 1.5em;
  font-weight: bold;
  margin: 0 0 2em;
}

.main__navigation_text {
  margin: 0 0 2em;
}

.main__navigation_lessons-search {
  margin: 0 0 2em;
  width: 100%;
}

.main__navigation_lessons-search > form {
  display: block;
  background-color: var(--text-secondary);
  padding: 15px;
}

.main__navigation_lessons-search > form > input {
  box-sizing: border-box;
  display: block;
  width: 100%;
  font-size: 1.2em;
  height: 40px;
  border: none;
  border-radius: 5px;
  padding-left: 15px;
}

.main__navigation_lessons-search > form > input:focus {
  outline: none;
}

.width_limiter {
  max-width: 800px;
  margin: 0 auto;
}
